import React from 'react';
import Title from "../../../components/title";
import GridView from "../../../containers/grid-view";
import {KEYS} from "../../../constants/keys";
import {URLS} from "../../../constants/urls";
import {get, head, last, split} from "lodash"
import downloadIcon from "../../../assets/icons/download.svg"
import {useNavigate} from 'react-router-dom'
import {NumericFormat} from "react-number-format";
import cloudIcon from "../../../assets/icons/Folders.svg";
import monitorIcon from "../../../assets/icons/monitor.svg";
import {useTranslation} from "react-i18next";


const InpatientReportsContainer = () => {
    const navigate = useNavigate()
    const {t} = useTranslation()

    const columns = [
        {
            title: t('Month'),
            key: 'month',
        },
        {
            title: t('СЛУЧАИ ФГМС'),
            key: 'fgms_count',
            render: ({value}) => <NumericFormat thousandSeparator={" "} displayType={'text'} value={value}/>
        },
        {
            title: t('СУММА ДЛЯ ОПЛАТЫ'),
            key: 'fgms_payment',
            render: ({value}) => <NumericFormat thousandSeparator={" "} displayType={'text'} value={value}/>
        },
        {
            title: t('ДРУГИЕ СЛУЧАИ'),
            key: 'total_count',
            render: ({value}) => <NumericFormat thousandSeparator={" "} displayType={'text'} value={value}/>
        },
        {
            title: t('СУММА'),
            key: 'total_payment',
            render: ({value}) => <NumericFormat thousandSeparator={" "} displayType={'text'} value={value}/>
        },
        {
            title: t('ДЕЙСТВИЯ'),
            key: 'organization_level',
            classnames: '!text-center',
            render: ({row}) => <div className={'flex justify-around items-center px-3'}>
                <img className={'mr-1'} src={cloudIcon} alt="cloud"/>
                <img src={monitorIcon} alt="cloud"/>
            </div>
        }

    ]
    return (
        <div>
            <div className="grid grid-cols-12 items-center">
                <div className="col-span-8">
                    <Title>{t("Стационарные отчеты")}</Title>
                </div>
                <div className="col-span-4 text-right">
                    <button
                        className={'inline-flex items-center py-2.5 pl-2.5 pr-5 text-sm font-bold text-primary border border-primary rounded-lg'}>
                        <img className={'mr-2.5'} src={downloadIcon} alt={'export'}/>
                        {t("Export")}
                    </button>
                </div>
                <div className="col-span-12 mt-8">
                    <GridView
                        onRowClick={(_row) => navigate(`/hospitals/inpatient-reports/${head(split(get(_row, 'month', ""), '/', 2))}/${last(split(get(_row, 'month', ""), '/', 2))}`)}
                        listKey={KEYS.inpatientReports} url={URLS.inpatientReports}
                        columns={columns}/>
                </div>
            </div>
        </div>
    );
};

export default InpatientReportsContainer;